.carousel .ant-carousel{
    margin: 78px 18px 0px 5px;
    border-radius: 4px;
}

/* .carousel ul li{
    width: fit-content !important;
} */

.ant-carousel .slick-dots li{
    width: 30px !important;
    margin-right: 5px !important;
}

.ant-carousel .carousel__img{
    width: 100%;
    height: 302px;
}

 .left__arrow{
 
    position: absolute;
    width: 52px;
    height: 104px;
    left: 0px;
    top: 37%;
    background-color: #ffffff;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom-right-radius: 4px;
    border-top-right-radius: 4px;
    color: black;
    box-shadow: 0 1px 5px 0 rgb(0 0 0 / 11%);
    transition: opacity .1s ease-in;
    /* display: none; */
}

.right__arrow{
  
    position: absolute;
    width: 52px;
    height: 104px;
    right: 0px;
    top: 37%;
    background-color: #ffffff;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom-left-radius: 4px;
    border-top-left-radius: 4px;
    color: black;
    box-shadow: 0 1px 5px 0 rgb(0 0 0 / 11%);
    transition: opacity .1s ease-in;
    /* display: none; */
}

@media screen and (max-width:500px) {
    .right__arrow,.left__arrow{
        display: none;
    }
}



.product__category{
background-color: #ffffff;
display: flex;
margin-bottom: 10px !important;
padding: 5px 0px 5px 5px;
justify-content: space-around;
margin: auto;
border-bottom: 1px solid #0000002e;
}

.category{
    display: flex;
    flex-direction: column;
    width: 63px;
    margin: 8px;
    cursor: pointer;
    align-items: center;
}

.category:hover span{
    color: #2874f0;
}

.category__img{
    width: 100%;
    height: 100%;
}

span{
    font-weight: 600;
    /* text-align: center; */
    line-height: 1.4;
    font-size: 15px;
    letter-spacing: 1px;
}

.product__category::-webkit-scrollbar{
    display: none;
}

/* media queries */
@media  screen and (max-width:500px) {
    .category{
        width: 47px;
    }

    .category span{
        font-size: 9px;
    }

    .product__category{
        margin-bottom: 0px  !important;
        padding: 0px;
    }
}
.product__card__container {
  margin: 10px 0px;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
}

.product__card__label {
  padding: 15px 20px;
  border-bottom: 1px solid #0000002e;
  /* border-top: 1px solid #0000002e; */
}

.product__card__label > h2 {
  font-size: 22px;
  line-height: 32px;
  font-weight: 500;
  margin: 0px;
}

.product__card__container .carousel ul{
  grid-gap: 15px;
  gap: 15px;
}

.product__card__container .carousel ul li{
  width: -webkit-fit-content !important;
  width: -moz-fit-content !important;
  width: fit-content !important;
}

.product__card__container .react-multiple-carousel__arrow {
  position: absolute;
  outline: 0;
  transition: all .5s;
  border-radius: 35px;
  z-index: 11;
  border: 0;
  background: rgba(0,0,0,.5);
  min-width: 43px;
  min-height: 43px;
  opacity: 1;
  cursor: pointer;
}

/* media queries */
@media screen and (max-width: 500px) {
  .product__card__container {
    margin: 0px;
    border-bottom: none;
  }
  .product__card__label {
    padding: 5px 20px;
  }
  .product__card__label > h2 {
    margin: 0px;
    font-size: 20px;
  }
}
@media screen and (max-width: 465px) {
  .product__card__container .react-multi-carousel-list li {
    display: flex;
    justify-content: center;
  }
}

.card__container{
    display: flex;
    flex-direction: column;
    padding: 16px;
